const columnsDetailCrawlSetting =
    [
        {title:'', field:'kbn', cellStyle:{fontSize:"0.8em", width:"15%", minWidth:"5%"}, headerStyle:{fontWeight:"bold",  width:"5%", minWidth:"5%"}},
        {title:'', field:'kbnname', cellStyle:{fontSize:"0.8em", width:"25%", minWidth:"15%"}, headerStyle:{fontWeight:"bold", width:"15%", minWidth:"15%"}},
        {title:'ID', field:'jigyosyaid', cellStyle:{fontSize:"0.8em", width:"25%", minWidth:"10%"}, headerStyle:{fontWeight:"bold", width:"10%", minWidth:"10%"}},
        {title:'', field:'name', cellStyle:{fontSize:"0.8em", width:"25%", minWidth:"15%"}, headerStyle:{fontWeight:"bold", width:"15%", minWidth:"15%"}},
    ]

export const columnsData = (param) => {
    switch(param) {
        case 1:
            return columnsCrawlSetting;
        case 2:
            return columnsKubunSetting;
        case 3:
            return columnsDetailCrawlSetting;
        default:
            return columnsCrawlSetting;
    }
};


